*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Manrope", sans-serif;
}

.card__heading {
  font-size: 1rem;
}

.card__advice {
  font-size: 1.75rem;
  background-color: #000;
}

.card {
  background-color: hsl(217deg, 19%, 38%);
}

body {
  width: 100%;
  min-height: 100vh;
  display: grid;
  place-items: center;
  grid-template-rows: 1fr min-content;
  background-color: hsl(218deg, 23%, 16%);
}